import React from 'react';
import { Container } from 'react-bootstrap';
import CardForSpeaker from './CardForSpeaker';
import 'bootstrap/dist/css/bootstrap.min.css';
import kSpeakerList from "../../Assets/KeynoteSpeakers.json";

function Talks() {
    return (
        <Container>
            {kSpeakerList.map((speaker) => (
            <CardForSpeaker
            id={speaker.id}
            talk = "Keynote Talk" 
            name = {speaker.name + " (" + speaker.affiliation + ")"}
            title = {speaker.title}
            abstract = {speaker.abstract}            
            bio =  {speaker.bio}            
            />
            ))}
        </Container>
        
    );
  }
  
export default Talks;